import axios, { type AxiosResponse } from 'axios'
import { useEffect, useRef, useState } from 'react'
import { useAuthHeader } from 'react-auth-kit'
import { useParams } from 'react-router-dom'

import { Box, Checkbox, Divider, Grid, TextField, Typography } from '@mui/material'
import type { GridRowSelectionModel } from '@mui/x-data-grid'
import {
  DataGrid,
  GridToolbarColumnsButton,
  GridToolbarContainer,
  GridToolbarDensitySelector,
  GridToolbarExport,
  GridToolbarFilterButton,
  GridToolbarQuickFilter
} from '@mui/x-data-grid'

import { CONFERENCE_REGISTER_COLUMNN } from 'src/data/conferenceRegister'

import { type ClubMember } from 'src/models/clubMember'

import CancelSubmitBtns from 'src/components/CancelSubmitBtns'

import { PureLightTheme } from 'src/theme/schemes/PureLightTheme'

interface ConferenceInfoProps {
  title: string
  changeTitle: (title: string) => void
  participantTarget: string
  changeParticipantTarget: (participantTarget: string) => void
  selectedMembers: number[]
  setSelectedMembers: React.Dispatch<React.SetStateAction<number[]>>
  handleSubmit?: () => void
}

const ConferenceInfo: React.FC<ConferenceInfoProps> = ({
  title,
  changeTitle,
  participantTarget,
  changeParticipantTarget,
  selectedMembers,
  setSelectedMembers,
  handleSubmit
}) => {
  const [members, setMembers] = useState<ClubMember[]>([])
  const [levels, setLevels] = useState<string[]>([])
  const [isPausedListing, setIsPausedListing] = useState<boolean>(true)
  const [tableWidth, setTableWidth] = useState(0)

  const { id } = useParams()
  const clubId = Number(id)
  const authHeader = useAuthHeader()
  const tableRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    axios
      .get(`${process.env.REACT_APP_APIHOST}/club/${clubId}/member`, {
        headers: { Authorization: authHeader() }
      })
      .then((res: AxiosResponse<ClubMember[]>) => {
        const activeMembers = res.data.filter(member => member.deletedAt === null && member.firedAt === null)
        const sortedActiveMembers = activeMembers
        sortedActiveMembers.sort((a, b) => (a.name < b.name ? -1 : 1))
        setMembers(sortedActiveMembers)

        const levels = Array.from(
          new Set(
            activeMembers
              .filter((member: ClubMember) => member.level !== null)
              .map((member: ClubMember) => member.level)
          )
        ).sort()
        setLevels(levels)
      })
  }, [])

  useEffect(() => {
    setTableWidth(tableRef.current?.clientWidth || 450)
  }, [])

  useEffect(() => {
    window.addEventListener('resize', getTableWidth)
  }, [])
  const getTableWidth = () => {
    if (tableRef.current) {
      setTableWidth(tableRef.current.clientWidth)
    }
  }

  const conferenceMemberRows = members.map(member => ({
    id: member.id,
    name: member.name,
    studentId: member.studentId,
    department: member.department,
    level: member.level,
    isPaused: member.isPaused
  }))

  const handleIsPausedChange: React.ChangeEventHandler<HTMLInputElement> = event => {
    setIsPausedListing(() => event.target.checked)
  }

  const handleRowSelectionChange = (selectedMemberIds: GridRowSelectionModel) => {
    const selectedIDs = new Set(selectedMemberIds)
    const selectedRows = conferenceMemberRows.filter(row => selectedIDs.has(row.id))
    setSelectedMembers(selectedRows.map(row => row.id))
  }

  const Toolbar = () => {
    return (
      <GridToolbarContainer sx={{ justifyContent: 'space-between' }}>
        <GridToolbarColumnsButton />
        <GridToolbarFilterButton sx={{ '& svg': { width: '18px', height: '18px' } }} />
        <GridToolbarDensitySelector />
        <GridToolbarExport />
        <GridToolbarQuickFilter />
      </GridToolbarContainer>
    )
  }

  return (
    <Grid container sx={{ justifyContent: 'center' }}>
      <Grid item xs={12} md={8}>
        <Box>
          <Box display="flex" justifyContent="space-between" alignItems="center" flexWrap="wrap" mt={1} mb={0.5}>
            <Box display="flex" alignItems="baseline">
              <Typography variant="h4" color={PureLightTheme.colors.primary.main} display="inline-block" whiteSpace='nowrap'>
                {'참여인원 '}
              </Typography>
              <Typography variant="caption" fontSize={1} display="inline-block">
                가나다순
              </Typography>
              <Typography variant="caption">행사에 참여해야하는 회원을 모두 체크해주세요</Typography>
            </Box>

            <Box>
              <Typography display="inline-block" variant="body1">
                휴회원 포함
              </Typography>
              <Checkbox sx={{ padding: '5px' }} checked={isPausedListing} onChange={handleIsPausedChange} />
            </Box>
          </Box>
          <DataGrid
            autoHeight
            checkboxSelection
            rowSelectionModel={selectedMembers}
            onRowSelectionModelChange={handleRowSelectionChange}
            rows={conferenceMemberRows}
            columns={CONFERENCE_REGISTER_COLUMNN}
            slots={{ toolbar: Toolbar }}
            disableRowSelectionOnClick
          />
        </Box>
      </Grid>
      <Grid item xs={12} md={4}>
        <Box paddingX={{ xs: 3 }} paddingBottom={{ xs: 0 }}>
          <Typography variant="h4" color={PureLightTheme.colors.primary.main} sx={{ margin: '20px 0px' }}>
            행사명
          </Typography>
          <TextField
            required
            id="title"
            label="행사명"
            value={title}
            onChange={event => changeTitle(event.target.value)}
            fullWidth
          />
          <Typography variant="h4" color={PureLightTheme.colors.primary.main} sx={{ margin: '20px 0px' }}>
            참여 대상
          </Typography>
          <TextField
            id="participantTarget"
            label="행사에 참여해야하는 회원 그룹을 메모해주세요 (예시: 운영진 전체, 신입회원)"
            value={participantTarget}
            onChange={event => changeParticipantTarget(event.target.value)}
            fullWidth
          />
          {handleSubmit && (
            <>
              <Divider sx={{ marginTop: 2, marginBottom: 2 }} />
              <CancelSubmitBtns submitBtnText="등록/수정" handleSubmit={handleSubmit} />
            </>
          )}
        </Box>
      </Grid>
    </Grid>
  )
}

export default ConferenceInfo
